<template>
<div v-show="pie == true">
     <div v-show="pie == true && loading == true">
        <v-responsive class="rounded-lg">
        <v-skeleton-loader
        width="1200"
        height="150"
        type="image"
        class="mx-auto"
        >
        </v-skeleton-loader>
        <v-skeleton-loader
        width="1200"
        height="150"
        type="image"
        class="mx-auto"
        >
        </v-skeleton-loader>
    </v-responsive>
    </div>

    <div v-show="pie == true && loading == false" class="mx-n3">
      <!-- mobile width 370 -->
    <ejs-accumulationchart  width='600' height='350' id="container" :tooltip='tooltip' :legendSettings='legendSettings'>
            <e-accumulation-series-collection >
                <e-accumulation-series :dataSource='seriesData' xName='tanggal' yName='terlambat' innerRadius='0%' :dataLabel='datalabel'> </e-accumulation-series>
            </e-accumulation-series-collection>
        </ejs-accumulationchart>
    </div>
</div>
</template>

<script>
import Vue from "vue";
import { AccumulationChartPlugin, PieSeries, AccumulationTooltip,AccumulationLegend,AccumulationDataLabel   } from "@syncfusion/ej2-vue-charts";
import api from "@/services/http";
Vue.use(AccumulationChartPlugin);

export default {
  props:['pie','daritanggal','sampaitanggal','MenuFilterTanggal'],
  data() {
return {
  show: true,
  seriesData1:[],
  token:null,
  loading:true,
  seriesData: [
        // { tanggal: new Date("2021, 03, 01"), terlambat: 5 }, 
        // { tanggal: new Date("2021, 03, 02"), terlambat: 2 },
        // { tanggal: new Date("2021, 03, 03"), terlambat: 1 }, 
        // { tanggal: new Date("2021, 03, 04"), terlambat: 4 },
        // { tanggal: new Date("2021, 03, 05"), terlambat: 1 }, 
        // { tanggal: new Date("2021, 03, 06"), terlambat: 9 },
        // { tanggal: new Date("2021, 03, 07"), terlambat: 6 }, 
        // { tanggal: new Date("2021, 03, 08"), terlambat: 3 },
        // { tanggal: new Date("2021, 03, 09"), terlambat: 12 }, 
        // { tanggal: new Date("2021, 03, 10"), terlambat: 15 },
        // { tanggal: new Date("2020, 6, 21"), terlambat: 0 }, { tanggal: new Date("2020, 6, 22"), terlambat: 1 }
          ],
  datalabel: { visible: true, name: 'text', position: 'Inside',
        connectorStyle:{
            //Length of the connector line in pixels
            length: '50px',
            //Width of the connector line in pixels
            width: 2,
            //dashArray of the connector line
            dashArray: '5,3',
            //Color of the connector line
            color: '#f4429e',
            //Specifies the type of the connector line either Line or Curve
            type: 'Curve'
        } },
    primaryXAxis: {
      valueType: 'DateTime',
      labelFormat: 'dd-MMM',
      rangePadding: 'Additional',
    },
    border: {
        color: 'green',
        width: 2
    },
    primaryYAxis:{
        labelFormat: '{value} Karyawan',
    },
    legendSettings: {
        width: '120', height: '250', border: { width: 1, color: 'grey'}
    },
    marker: {
    dataLabel:{
            visible: true, width: 10, height: 10
        }
    },
    tooltip: { enable: true, header: 'Terlambat'},
  title: "Analisis Keterlambatan"
};
  },
  provide: {
      accumulationchart: [PieSeries, AccumulationTooltip,AccumulationLegend,AccumulationDataLabel  ]
  },
  watch:{
    MenuFilterTanggal(){
      if (this.daritanggal !=null && this.sampaitanggal != null) {
        this.getdata()
      }
    },
  },
  mounted(){
    this.token = localStorage.getItem('token')
    // this.getdata()
    this.data7day()
  },
  methods:{
    data7day(){
      function formattanggal(x) {
          var todayTime = new Date(x);
          var bulan = ["Jan", "Feb", "Mar", "Apr", "Mei", "Jun", "Jul", "Agu", "Sep", "Okt", "Nov", "Des"];
          var month = bulan[todayTime .getMonth()];
          var day = todayTime .getDate();
          var year = todayTime .getFullYear();
          return day + " " + month + " " + year; 
      }
        let today=new Date();
        const days_to_subtract=7;
        let tgl= new Date(today.valueOf()-(days_to_subtract*24*60*60*1000)).toISOString().slice(0,10);
        let tglskrng = today.toISOString().slice(0,10)

      api.get('/analisisterlambat/'+tgl+'/'+tglskrng+'/?token='+this.token ,{ headers: {"Authorization" : `Bearer ${this.token}`} }).then(
				res => {
          this.seriesData1 = res.data
          this.loading = false
          for (let index = 0; index < this.seriesData1.length; index++) {
                if(!this.seriesData1[index].terlambat){
                  //do nothing
                }else{
                  this.seriesData1[index].tanggal = formattanggal(this.seriesData1[index].tanggal)
              }
            }
          if (this.seriesData1.length == 0) {
            this.seriesData = []
          }else{
            this.seriesData = this.seriesData1
          }
          
				},
				err => {
					console.log(err);
				}
			)
    },
    getdata(){
      function formattanggal(x) {
          var todayTime = new Date(x);
          var bulan = ["Jan", "Feb", "Mar", "Apr", "Mei", "Jun", "Jul", "Agu", "Sep", "Okt", "Nov", "Des"];
          var month = bulan[todayTime .getMonth()];
          var day = todayTime .getDate();
          var year = todayTime .getFullYear();
          return day + " " + month + " " + year;
      }
      
      api.get('/analisisterlambat/'+this.daritanggal+'/'+this.sampaitanggal+'/?token='+this.token ,{ headers: {"Authorization" : `Bearer ${this.token}`} }).then(
				res => {
          this.seriesData1 = res.data
          this.loading = false
          for (let index = 0; index < this.seriesData1.length; index++) {
                if(!this.seriesData1[index].terlambat){
                  //do nothing
                }else{
                  this.seriesData1[index].tanggal = formattanggal(this.seriesData1[index].tanggal)
              }
            }
          if (this.seriesData1.length == 0) {
            this.seriesData = []
          }else{
            this.seriesData = this.seriesData1
          }
          
				},
				err => {
					console.log(err);
				}
			)
    },

  },
};
</script>
<style>
 /* #container {
   height: 450px;
 } */
</style>